
import { dataFormat } from '../../../frame_template/js/utils.js'
import zPopup from '../module/Popup'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {

components: {
zPopup,
Group,
XInput,
XTextarea,
Datetime,
Selector,
Cell
},

data() {
return {
value: '',
value2: '',
date: {
time: ''
},
tableData: [],
dialogTime: false,
dialogCreate: false,
dialogSchoolDetail: false,
tableTime: [{time: ''}],
schoolName:'',
projectId: localStorage.getItem('projectId'),
townId: localStorage.getItem('townId'),
roleId: localStorage.getItem('roleId'),
year: '',
shenbao:'',
stage: '',
stageList: [],
schoolTypeList: [],
form:{
startYear:'',
endYear:'',
area:'',
newBuild:'',
schoolName:'',
reward:'',
fileStr:'',
projectId: localStorage.getItem('projectId'),
townId: localStorage.getItem("townId"),
id: this.$route.params.schoolId,
schoolType:'',
invest:''
},
project: {},
fileList: [],
schooldDetail: {},

}
},

computed: {
fileLists() {
let newArr = [];
this.fileList.forEach((item, index) => {
newArr.push({
name: item.fileName,
url: item.fileUrl
})
})
console.log(newArr);
return newArr;
}
},

mounted() {
this.$nextTick(() => {

this.initSchoolList();
this.queryStage();
this.querySchoolType();
})
},

methods: {
initSchoolList(){
let temp = this;
this.$post('/web/querySchoolList', {
"projectId": temp.projectId,
"schoolName": temp.schoolName,
"stage": this.stage,
"shenbao":temp.shenbao,
"townId": temp.townId,
"year":temp.year,
"pageSize": 10000
}).then((m)=> {
if(m.resultCode == 0){
temp.tableData = m.content.page.content;
}else{
temp.tableData = [];
}
})

this.$post('/web/queryTownProject', {
"projectId":this.projectId,
"townId":this.townId,
}).then((m)=> {
if(m.resultCode == 0){
temp.project = m.content.project;
}
})
},
showDateValue(time) {
return dataFormat(time, 'YYYY-MM-DD')
},
yearMonthDate() {
let temp = this;
this.$vux.datetime.show({
cancelText: '',
confirmText: '',
format: 'YYYY-MM',
value:  dataFormat(new Date(), 'YYYY-MM'),
onShow () {

},
onHide () {
temp.year = this.value;
temp.initSchoolList();
}
})
},
showDate (item) {
let _this = this;
var date;
if(item.time==null){
date = new Date();
}else{
date = item.time;
}

this.$vux.datetime.show({
cancelText: '',
confirmText: '',
format: 'YYYY-MM-DD',
value:  dataFormat(date, 'YYYY-MM-DD'),
onShow () {

},
onHide () {
item.time = this.value
}
})
},

addTime() {
this.daopaiList.push({name:'',time:'',isFinish:0});
},
saveDaopai(){
var temp = this;
this.$post('/web/shezhiDaopaishijian', {
"schoolId": this.schoolId,
"timeStr": JSON.stringify(this.daopaiList),
}).then(function(m) {
temp.$vux.toast.show({
text: '!',
width: '3rem'
})
temp.dialogTime = false;
})
},
shezhi(id){
var temp = this;
temp.schoolId = id;
this.$post('/web/queryDaopaishijian', {
"schoolId": id
}).then(function(m) {
temp.daopaiList=m.content.list;
temp.dialogTime = true;
})
},
handleRemove(file, fileList) {
console.log(file, fileList);
},
handlePreview(file) {
console.log(file);
},
handleExceed(files, fileList) {
},
beforeRemove(file, fileList) {
let _this = this;

return new Promise((reslove, reject) => {
this.$vux.confirm.show({
title: '',
content: ` ${ file.name }`,
onCancel () {
reject();
},
onConfirm () {
_this.$vux.loading.show({
transition: '',
text: '...'
})
setTimeout(() => {
_this.$vux.loading.hide();
reslove();
}, 1000)
}
})
})
},
queryStage(){
var temp = this;
this.$post('/web/queryStage', {
"projectId":this.projectId
}).then(function(m) {
temp.stageList = m.content.list;
temp.stageList.unshift({id: '',name:''});
})
},
createSchool(){
this.dialogCreate = true;
},
querySchoolType(){
var temp = this;
this.$post('/web/queryDic', {"pcode":"101"}).then(function(m) {
temp.schoolTypeList=m.content.list;
temp.schoolTypeList.unshift({code: '',name:''});
})
},
save(){
this.form.fileStr = JSON.stringify(this.fileList);
if(this.form.schoolName == ''){
this.$vux.toast.show({
text: '',
width: '3rem'
})
return;
}
if(this.form.schoolType == ''){
this.$vux.toast.show({
text: '',
width: '3rem'
})
return;
}
if(this.form.invest == ''){
this.$vux.toast.show({
text: '',
width: '3rem'
})
return;
}
if(this.form.startYear == ''){
this.$vux.toast.show({
text: '',
width: '3rem'
})
return;
}
if(this.form.endYear == ''){
this.$vux.toast.show({
text: '',
width: '3rem'
})
return;
}
if(this.form.area == ''){
this.$vux.toast.show({
text: '',
width: '3rem'
})
return;
}
if(this.form.newBuild == ''){
this.$vux.toast.show({
text: '',
width: '3rem'
})
return;
}
this.$post("/web/addSchool", this.form).then((m) => {
if(m.resultCode == 0) {
this.$vux.toast.show({
text: '!',
width: '3rem'
})
this.quxiao();
this.initSchoolList();
}
})

},
quxiao(){
this.dialogCreate = false;
},
handleRemove(file, fileList) {
if(this.removeFlag) {
var index = this.fileLists.indexOf(file);
this.fileLists.splice(index,1);
this.fileList.splice(index,1);
}
},
newhandlesuccess(file,fileList){
if(file.resultCode == 0){
this.fileList.push({"fileName":file.content.fileName,"fileUrl": file.content.fileUrl});
}else if(file.resultCode == 'tolarge'){
this.$vux.toast.show({
text: '50M!',
width: '3rem'
})
var index = this.fileLists.indexOf(file);
this.fileLists.splice(index,1);
this.fileList.splice(index,1);
return;
}
},
turnTo(name,row,val){
this.$router.push({
name: name,
params:{
projectId:row.projectId,
schoolId:row.id,
edit:val,
schoolScheduleId:row.schoolScheduleId
}
})

},
turnDetail(row){
this.schooldDetail = row;
console.log(this.schooldDetail);
this.dialogSchoolDetail = true;
},

}
}



