
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'

export default {
name: "zPopup",

directives: {
TransferDom
},

components: {
Popup,
},

data() {
return {

}
},

props: {
title: String,
show: {
type: Boolean,
default: false
}
},

computed: {
isShow: {
get() {
return this.show
},
set(val) {
this.$emit('update:show', val);
}
}
},

beforeCreate() {
document.querySelector('.app-view-inner').style.overflow = 'hidden'
},

beforeDestroy() {
document.querySelector('.app-view-inner').style.overflow = 'auto'
},

mounted() {
},

methods: {

}
}



